// src/app/page.tsx
"use client";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

export default function HomePage() {
  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center justify-center p-8">
      
      {/* Título */}
      <h1 className="text-3xl font-bold text-gray-900 mb-8">
        Shadcn UI Funciona! ✅
      </h1>

      {/* Botones - sin variant */}
      <div className="flex gap-4 mb-8">
        <Button>Botón Default</Button>
        <Button className="border-2 border-blue-500 bg-transparent text-blue-600 hover:bg-blue-50">
          Botón Outline
        </Button>
        <Button className="bg-transparent hover:bg-gray-100">
          Botón Ghost
        </Button>
      </div>

      {/* Tarjeta */}
      <Card className="p-6 max-w-md w-full bg-white shadow-lg">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">
          Esto es una Card
        </h2>
        <p className="text-gray-600 mb-4">
          Componente Card de Shadcn UI funcionando correctamente
        </p>
        <Button className="w-full">Acción en la Card</Button>
      </Card>

      {/* Badges - sin variant */}
      <div className="mt-8 flex gap-3">
        <Badge>Default</Badge>
        <Badge className="bg-gray-200 text-gray-800">
          Secondary
        </Badge>
        <Badge className="border-2 border-gray-300 bg-transparent text-gray-700">
          Outline
        </Badge>
      </div>

    </div>
  );
}